* {
    box-sizing: border-box;
    
}

body {
    margin: 0;
}

.nav {
    background-color: #333;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
    align-items: stretch;
}


.nav ul {
    padding: 0;
    margin: 0;
    display: flex;
    gap: 1rem;
    list-style: none;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    padding: .25rem;
    font-size: 30px;
}

.nav li:active {
    background-color: #555;
}

.nav li:hover{
    background-color: #777;
    cursor: pointer;
}

.home-page {
    background-image: linear-gradient(40deg, rgb(65, 206, 245), #9c00fd);
    padding-top: 100px;
    padding-bottom: 100px;
}

.project_container{
    border:solid 4px;
    border-color: rgb(232, 232, 232);
    background-color:  rgb(232, 232, 232);
    width:60%;
    margin-top: 50px;
    margin-left: 35%;
    display: block;
    margin-bottom: 20px;
    border-radius: 30px;
}

.title{
    display: inline-block;
    border: solid 2px;
    border-color: rgb(138, 73, 202);
    border-radius: 30px;
    width:30%;
    margin:10px;
    text-align: center;
    color: rgb(138, 73, 202);
}

.info{
    display:inline-block;
    width:200px;
    padding: 30px;
}

.info_container{
    text-align: right;
    margin-right:50px;
    
}

.progress-bar {
    display: inline-block;
    width:60%;
    padding-left: 140px;
    
    
 
}


.dropdown-menu{
    width:50%;
}


